body{

    font-size: 2emf;
  }
  
  .image{
  width:12%;
  height:65% ;
  border-radius:50%;
  margin-left: 17%;
  margin-top:2.5% ;
  
  }
  
  #header{
  
    background-color: rgb(202, 112, 190);
    width: 100%;
    height:35% ;
    display: flex;
    flex-direction: row;
    align-content: space-between
  
    
  }
  
  
  
  .div1{
  margin-top: 3%;
  margin-left: 10%;
  color: beige;
  
  }
  
  .section2{
  display: flex;
  flex-direction: row;
  width: 100%;
  
  }
  
  .colm1{
  
  width: 50%;
  text-align: center;
  margin-left: 5%;
  
  }
  
  .colm2{
  
    width: 50%;
    
    
    }
  
  #tit1{
  
   font-size: 3.5vw;
  }   
  
  #tit2{
  
    font-size: 2.5vw;
  }   
  
   
  #tit3{
  
    font-size: 1.5vw;
  }   
  
     
    .contact{
  
      font-size: 2.7vw; 
      font-weight: bold;
       margin-top: 8%; 
    }
  
  
    .achivement{
        font-size: 2.5vw;
         font-weight: bold; 
         margin-top: 7%;
          background-color: rgb(225, 150, 248); 
          width: 70%;
          margin-left: 5%;
  
  
    }
  
    .icons{
  
        display: flex;
         flex-direction: column; 
         align-items: flex-start;
          margin-left: 27%;
          
        
    }
    .fa {
        
        transform: scale(1.1,1.2); 
        margin-top: 5%;
        text-align: center;
        box-sizing: border-box;
        color: rgb(78, 41, 112);
  
    }
  
    .iconFont{
        font-size: 1em;
         color: black;
  
  
    }
  
    .skill{
  
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        margin-left: 27.5%;
        margin-top: 5%;
    
        
    }
    .skillsFont{
        font-weight: bold;
    }
  
    .fa1 {
        
        transform: scale(4,4); 
        margin-top: 5%;
        text-align: center;
        box-sizing: border-box;
        color: orange;
       
    
    }
    .fa2{
        
        transform: scale(4,4); 
        margin-top: 5%;
        text-align: center;
        box-sizing: border-box;
        color: red;
        margin-left: 15%;
    
    }
    .fa3{
        
        transform: scale(4,4); 
        margin-top: 5%;
        text-align: center;
        box-sizing: border-box;
        color:green;
        margin-left: 15%;
    
    }
    .fa4{
        
        transform: scale(4,4); 
        margin-top: 5%;
        text-align: center;
        box-sizing: border-box;
        color:yellow;
        margin-left: 15%;
    
    }
  
    .info{
  
        display: flex;
        flex-direction: row;
        margin-left: 22%;
        margin-top: 5%;
  
  
    }
    .fa5{
        
        transform: scale(2.5,2.5); 
        margin-top: 3%;
        text-align: center;
        box-sizing: border-box;
        color:rgb(53, 53, 245);
        margin-left: 10%;
    
    }
    .fa6{
        
        transform: scale(2.5,2.5); 
        margin-top: 3%;
        text-align: center;
        box-sizing: border-box;
        color:rgb(190, 32, 32);
        margin-left: 35%;
    
    }
    .progress{
   display: flex;
   flex-direction: column; 
   align-items: flex-start;
   margin-top: 4%;
   margin-left: 25%;
  
  
  
    }
  
    .footer{
         display: flex;
         flex-direction: column;
         align-items: flex-start;
         flex-wrap: wrap;
         margin-left: 10%;
         margin-top: 1%;
         margin-right: 10%;
        font-size: 1em;
    }
  
  
  
  /* background: */
  #file {background-color: rgb(248, 241, 241); width:100%;}
  #file  {background-color: rgb(231, 221, 221); width: 300; height: 20;}
  #file::-webkit-progress-value {background-color: orange !important;}
  
  #file3 {background-color: rgb(248, 241, 241); width:100%;}
